import { NavLink, useNavigate } from 'react-router-dom';

import iconBell from '../../images/icon-bell.svg';
import iconProfile from '../../images/icon-profile.svg';
import iconBack from '../../images/icon-back.svg';
import './Header.css';

// sectionName='name':  ,
// newtask:     ,
// branch='name':     
function Header({ sectionName, newtask, branch, timer }) {
	const navigate = useNavigate();

	return (
		<div className="header">
			<div className="header__container">
				{sectionName && <h2 className="header__section">{sectionName}</h2>}
				{newtask && (
					<button className="header__button-task"> </button>
				)}
				{branch && (
					<div className="header__nav-container">
						<img
							className="header__icon-back"
							src={iconBack}
							width={8}
							height={14}
							alt=" "
						/>
						<NavLink className="header__navlink" to={navigate(-1)}>
							{branch}
						</NavLink>
					</div>
				)}
				{timer && (
					<NavLink className="header__navlink-timer" to={navigate(-1)}>
						
					</NavLink>
				)}
			</div>
			<div className="header__icons-container">
				<img
					className="header__icon"
					src={iconBell}
					width={20}
					height={22}
					alt=" "
				/>
				<NavLink to="/profile">
					<img
						className="header__icon"
						src={iconProfile}
						width={40}
						height={40}
						alt=" "
					/>
				</NavLink>
			</div>
		</div>
	);
}

export default Header;
